import React from 'react';
import {render} from 'react-dom';
import {Provider} from 'react-redux';
import store from './script/store';

let root= document.getElementById('app');

store.dispatch({type: "MOVE"});

render(
	<Provider store={store}>
		<span>aaaaa</span>
	</Provider>,root
);


